import React from 'react';
import {getFunName} from '../helpers';

class StorePicker extends React.Component {
    // constructor(){
    //   super();
    //   this.goToStore = this.goToStore.bind(this);
    // }
    goToStore(event) {
        event.preventDefault();
        console.log("you changed the URL");
        // first grab the text form the box
        //second we are going to transition from / to /store:storeID
        const storeId = this.storeInput.value;
        this.context.router.transitionTo(`/store/${storeId}`)

    }

    render() {
        return (
            <form action="" className="store-selector" onSubmit={this.goToStore.bind(this)}>
                <h2>please enter a store</h2>
                <input type="text" required placeholder="Store Name" defaultValue={getFunName()} ref={(input) => {
                    this.storeInput = input
                }}/>
                <button type="submit">Please enter a store</button>
            </form>
        )
    }
}
StorePicker.contextTypes = {
  router: React.PropTypes.object
}


export default StorePicker



